<template>
  <div class="overviewPage">
    <h1>Overview</h1>
    <!-- <tc-filters :filterOptions="categories" filterName="Filter op Categories" />-->
    <button @click="newContract" class="button__text">Nieuw contract</button>
    <tc-contracts
      :contracts="contracts"
      :masterData="masterData"
      :files="files"
      :contacts="contacts"
    />
  </div>
</template>

<script>
// modals
import NewContractModal from "@/components/modals/NewContractModal.vue";

// components
import Contracts from "@/components/contracts/Contracts.vue";
// import Filters from "@/components/filters/filters";

export default {
  beforeCreate: function() {
    this.$store.dispatch("setContracts");
    this.$store.dispatch("setMasterData");
    this.$store.dispatch("setFiles");
    this.$store.dispatch("setDocuments");
    this.$store.dispatch("setContacts");
  },
  data: function() {
    return {
      categoriesArray: []
    };
  },
  computed: {
    contracts() {
      return this.$store.getters.getContracts;
    },
    masterData() {
      return this.$store.getters.getMasterData;
    },
    files() {
      return this.$store.getters.getFiles;
    },
    documents() {
      return this.$store.getters.getDocuments;
    },
    contacts() {
      return this.$store.getters.getContacts;
    }
  },
  methods: {
    newContract: function() {
      // const objReq = new XMLHttpRequest();
      // objReq.open("GET", "http://localhost:8765");
      // objReq.send();
      this.$modal.show(
        NewContractModal,
        {
          title: "Nieuw contract",
          id: -1,
          name: "",
          contacten: this.contacts
        },
        {
          scrollable: true,
          height: "auto"
        }
      );
    }
  },
  name: "Overview",
  components: {
    "tc-contracts": Contracts
    // "tc-filters": Filters
  }
};
</script>
