<template>
  <div class="editContractModal">
    <button @click="closeModal" class="close-modal">
      <close-icon fillColor="#2c3e50" class="icon-button" />
    </button>
    <h1>{{ title }}</h1>
    <vue-form
      name="editContractModel"
      :state="formstate"
      @submit.prevent="onSubmit"
      class="editContract__form"
    >
      <div class="editContract__form-container">
        <validate tag="label">
          <div class="editContract__form-row">
            <span>Naam contract*</span>
            <input
              placeholder="Vul een naam in"
              v-model="model.name"
              class="input__styling"
              required
              name="name"
            />
          </div>
          <div class="editContract__form-row">
            <span>Bestand</span>
            <multiselect
              name="files"
              v-model="model.file"
              :options="files"
              ref="multiSelectContacts"
              :multiple="false"
              :close-on-select="false"
              :clear-on-select="false"
              placeholder="Kies contract"
              label="name"
              track-by="name"
            ></multiselect>
          </div>
          <div class="editContract__form-row">
            <span>Categorie</span>
            <multiselect
              name="category"
              v-model="model.category"
              :options="categories"
              ref="multiSelectContacts"
              :multiple="false"
              :close-on-select="false"
              :clear-on-select="false"
              placeholder="Kies categorie"
              label="name"
              track-by="name"
            ></multiselect>
          </div>
          <div class="editContract__form-row">
            <span>Contacten</span>
            <div class="editContract__form-column-alt">
              <multiselect
                name="contacts"
                v-model="model.contacts"
                :options="contacts"
                ref="multiSelectContacts"
                :multiple="true"
                :close-on-select="false"
                :clear-on-select="false"
                placeholder="Kies de contact(en)"
                label="name"
                track-by="name"
              >
                <template slot="item" slot-scope="data" v-if="data.length > 0">
                  <span class="multiselect__single">{{ data }}</span>
                </template>
                <template
                  slot="selection"
                  slot-scope="{ values, data, search, isOpen }"
                  v-if="values.length && !isOpen"
                >
                  <span class="multiselect__single"
                    >{{ values.length }} contacten geselecteerd
                  </span>
                </template>
              </multiselect>
              <ul class="resources-list">
                <template v-for="(resource, index) in model.contacts">
                  <li
                    class="resource-item"
                    :data-index="index"
                    :key="resource.id"
                  >
                    <div class="resource-title" :id="index">
                      <span>{{ resource.name }}</span>
                      <span
                        v-on:click.prevent="removeItem(index, model.contacts)"
                        ><close-icon fillColor="#dedede" class="icon-button"
                      /></span>
                    </div>
                  </li>
                </template>
              </ul>
            </div>
          </div>
          <div class="editContract__form-row">
            <span>Notificaties in dagen</span>
            <div class="editContract__form-column-alt">
              <multiselect
                name="daysBeforenotification"
                v-model="model.daysBeforeNotification"
                :options="daysBeforeNotifications"
                :multiple="true"
                :close-on-select="false"
                :clear-on-select="false"
                placeholder="Kies de notificatie dag(en)"
                label="name"
                track-by="name"
              >
                <template slot="item" slot-scope="data" v-if="data.length > 0">
                  <span class="multiselect__single">{{ data }}</span>
                </template>
                <template
                  slot="selection"
                  slot-scope="{ values, data, search, isOpen }"
                  v-if="values.length && !isOpen"
                >
                  <span class="multiselect__single"
                    >{{ values.length }} dag(en) geselecteerd
                  </span>
                </template>
              </multiselect>
              <ul class="resources-list">
                <template
                  v-for="(resource, index) in model.daysBeforeNotification"
                >
                  <li
                    class="resource-item"
                    :data-index="index"
                    :key="resource.id"
                  >
                    <div class="resource-title" :id="index">
                      <span>{{ resource.name }}</span>
                      <span
                        v-on:click.prevent="
                          removeItem(index, model.daysBeforeNotification)
                        "
                        ><close-icon fillColor="#dedede" class="icon-button"
                      /></span>
                    </div>
                  </li>
                </template>
              </ul>
            </div>
          </div>
          <div class="editContract__form-row">
            <span>Bedrijf*</span>
            <multiselect
              name="customers"
              v-model="model.customer"
              :options="customers"
              ref="multiSelectContacts"
              :multiple="false"
              :close-on-select="false"
              :clear-on-select="false"
              placeholder="Kies bedrijf"
              label="name"
              track-by="name"
            ></multiselect>
          </div>
          <div class="editContract__form-row">
            <div class="editContract__form-column">
              <span>vanaf datum contract</span>
              <input
                name="contractDateFrom"
                class="input__styling"
                type="date"
                v-model="model.contractDateFrom"
                required
              />
            </div>
            <div class="editContract__form-column">
              <span>tot datum contract</span>
              <input
                name="contractDateTill"
                type="date"
                class="input__styling"
                v-model="model.contractDateTill"
                required
              />
            </div>
          </div>

          <field-messages name="name">
            <div slot="required">Naam is een verplicht veld</div>
          </field-messages>
          <field-messages name="company">
            <div slot="required">Bedrijf is een verplicht veld</div>
          </field-messages>
          <field-messages name="contractDateFrom">
            <div slot="required">Vanaf datum is een verplicht veld</div>
          </field-messages>
          <field-messages name="contractDateTill">
            <div slot="required">Tot datum is een verplicht veld</div>
          </field-messages>
        </validate>
      </div>
      <div class="buttons__container">
        <button class="buttons__button buttons__button--icon" type="submit">
          Opslaan
        </button>
      </div>
    </vue-form>
  </div>
</template>

<script>
export default {
  props: [
    "id",
    "name",
    "title",
    "category",
    "customer",
    "file",
    "contactValue",
    "contractDateFrom",
    "contractDateTill",
    "daysBeforeNotification",
    "contract_key"
  ],
  name: "edit-contract",
  data: function() {
    return {
      formstate: {},
      daysBeforeNotificationOptions: [],
      selectedResources: [],
      model: {
        name: this.name,
        category: this.category,
        customer: this.customer,
        file: this.file,
        contacts: this.contactValue,
        contractDateFrom: this.contractDateFrom,
        contractDateTill: this.contractDateTill,
        daysBeforeNotification: this.daysBeforeNotification
      }
    };
  },
  computed: {
    files() {
      return this.$store.getters.getFiles.map(({ fileId, path }) => {
        return { id: fileId, name: path };
      });
    },
    customers() {
      return this.$store.getters.getCustomers.map(({ id, name }) => {
        return { id: id, name: name };
      });
    },
    contacts() {
      return this.$store.getters.getContacts.map(({ id, name }) => {
        return { id: id, name: name };
      });
    },
    categories() {
      return this.$store.getters.getCategories.map(({ id, name }) => {
        return { id: id, name: name };
      });
    },
    daysBeforeNotifications() {
      function sortDays(a, b) {
        let comparison = 0;
        if (a.id > b.id) {
          comparison = 1;
        } else if (a.id < b.id) {
          comparison = -1;
        }
        return comparison;
      }
      return this.$store.getters.getDaysBeforeNotification
        .map(({ id, name }) => {
          return { id: id, name: name };
        })
        .sort(sortDays);
    },
    sendContactValue() {
      return this.model.contacts.map(({ id }) => {
        return id;
      });
    },
    sendDaysBeforeNotificationValue() {
      return this.model.daysBeforeNotification.map(({ id }) => {
        return id;
      });
    },
    masterData() {
      return this.$store.getters.getMasterData;
    }
  },
  beforeCreate: function() {
    this.$store.dispatch("setCategories");
    this.$store.dispatch("setCustomers");
    this.$store.dispatch("setFiles");
    this.$store.dispatch("setContacts");
    this.$store.dispatch("setDaysBeforeNotification");
    this.$store.dispatch("setMasterData");
  },
  methods: {
    onSubmit: function() {
      if (this.formstate.$invalid) {
        // alert user and exit early
        console.log("test");
        return;
      }
      this.$store.dispatch("editContract", {
        name: this.model.name,
        files: this.model.file,
        category: this.model.category,
        contacts: this.sendContactValue,
        daysBeforeNotification: this.sendDaysBeforeNotificationValue,
        customer: this.model.customer,
        contractDateFrom: this.model.contractDateFrom,
        contractDateTill: this.model.contractDateTill,
        contract_key: this.contract_key
      });
      this.$store.dispatch("setContracts");
      this.$emit("close");
    },
    closeModal: function() {
      this.$emit("close");
    },
    removeItem(index, itemSet) {
      itemSet.splice(index, 1);
    }
  }
};
</script>

