<template>
  <div
    v-if="categoryValue && customerValue && contract && files && fileFiltered"
  >
    <li :class="`cat__row cat-${categoryValue.id}`" v-if="contract.isFirst">
      <span class="contract__cat-name">{{ categoryValue.name }}</span>
      <span class="contract__img-header"></span>
      <span class="contract__name-header">Naam:</span>
      <span class="contract__customer-header">Bedrijf:</span>
      <span class="contract__document-header">Doc:</span>
      <span class="contract__period-header">Periode:</span>
      <span class="contract__daysto-header">Dagen:</span>
      <span class="contract__buttons-header"></span>
    </li>
    <li
      :class="
        `contract__row cat__id-${categoryValue.id} ${
          daysToDate(contract).expired === 'expired' ? 'expired' : ''
        }`
      "
    >
      <span class="contract__img">
        <img
          v-if="customerValue.image"
          :src="customerValue.image"
          class="customer__image"
        />
        <span v-else>
          <image-frame-icon fillColor="#2c3e50" />
        </span>
      </span>
      <span class="contract__name">
        {{ contract.name }}
      </span>
      <span class="contract__customer">
        {{ customerValue.name }}
      </span>
      <span class="contract__document">
        <a :href="pathTransformed" target="_blank"
          ><paperclip-icon fillColor="#2c3e50" class="icon-button" />
        </a>
      </span>
      <span class="contract__period">
        [{{
          moment(new Date(contract.dateTime[0].toDate())).format("DD-MM-YYYY")
        }}
        -
        {{
          moment(new Date(contract.dateTime[1].toDate())).format("DD-MM-YYYY")
        }}]
      </span>
      <span class="contract__daysto">
        {{ daysToDate(contract).returnValue }}
      </span>
      <span class="contract__buttons">
        <button v-on:click="editContract" class="button__icon">
          <pencil-outline-icon fillColor="#2c3e50" class="icon-button" />
        </button>
        <button v-on:click="deleteContract" class="button__icon">
          <delete-icon fillColor="#2c3e50" class="icon-button" />
        </button>
      </span>
      <span class="contract__progressBar">
        <ProgressBar
          :days="daysToDate(contract).progressValue"
          :expired="daysToDate(contract).expired"
        />
      </span>
    </li>
  </div>
</template>

<script>
import moment from "moment";
import ProgressBar from "@/components/progressbar/ProgressBar.vue";
import EditcontractModal from "@/components/modals/EditContractModal.vue";

export default {
  data: function() {
    return {};
  },
  beforeCreate: function() {
    this.$store.dispatch("setCategories");
    this.$store.dispatch("setCustomers");
    this.$store.dispatch("setDaysBeforeNotification");
  },
  computed: {
    fileFiltered: function() {
      return this.files.slice().filter((file) => {
        if (this.contract.documentId === file.fileId) {
          return file;
        }
      })[0];
    },
    categories() {
      return this.$store.getters.getCategories;
    },
    customers() {
      return this.$store.getters.getCustomers;
    },
    daysBeforeNotification() {
      return this.$store.getters.getDaysBeforeNotification;
    },
    pathTransformed() {
      return (
        "https://jasperhuting.stackstorage.com/api/download?download-path=" +
        this.fileFiltered.path +
        "&CSRF-Token=-If0CIL5Cgj9auGN62w"
      );
    },
    fileValue: function() {
      return this.files
        .slice()
        .filter((file) => {
          if (this.contract.documentId === file.fileId) {
            return file;
          }
        })
        .map(({ fileId, path }) => {
          return { id: fileId, name: path };
        });
    },
    categoryValue: function() {
      return this.categories.slice().filter((cat) => {
        if (cat.id === this.contract.category) {
          return cat;
        }
      })[0];
    },
    customerValue: function() {
      return this.customers.slice().filter((cust) => {
        if (cust.id === this.contract.customer) {
          return cust;
        }
      })[0];
    },
    contactsValue: function() {
      return this.contacts
        .slice()
        .filter((contact) => {
          // console.log(contact.id);
          // console.log(this.contract.contacts);
          if (this.contract.contacts.includes(contact.id)) {
            return contact;
          }
        })
        .map(({ id, name }) => {
          return { id: id, name: name };
        });
    },
    daysBeforeNotificationValue: function() {
      return this.daysBeforeNotification
        .filter((days) => {
          // console.log(contact.id);
          // console.log(this.contract.contacts);
          if (this.contract.daysBeforeNotification.includes(days.id)) {
            return days;
          }
        })
        .map(({ id, name }) => {
          return { id: id, name: name };
        });
    },
  },
  methods: {
    daysToDate(value) {
      const timeValue = moment(new Date(value.dateTime[1].toDate())).format(
        "YYYY-MM-DD"
      );
      // const timeValue = moment(new Date('2020', '03', '15')).format('YYYY-MM-DD');
      const monthDate = parseInt(timeValue.split("-")[1]);
      const dayDate = parseInt(timeValue.split("-")[2]);
      const currentMonth = new Date().getMonth() + 1;
      const currentDay = new Date().getDate();
      let countYear = new Date().getFullYear();
      if (monthDate < currentMonth) {
        countYear += 1;
      } else if (monthDate === currentMonth) {
        if (dayDate < currentDay) {
          countYear += 1;
        }
      }

      if (moment(timeValue).isBefore(new Date())) {
        countYear = parseInt(timeValue.split("-")[0]);
      }

      const date2 = new Date(countYear, monthDate - 1, dayDate);
      const date1 = new Date(
        new Date().getFullYear(),
        currentMonth - 1,
        new Date().getDate()
      );

      let res = Math.abs(date1.getTime() - date2.getTime()) / 1000;
      let days = Math.floor(res / 86400);
      if (moment(timeValue).isBefore(new Date())) {
        res = Math.abs(date2.getTime() - date1.getTime()) / 1000;
        days = Math.floor(res / 86400);
        let retValue = "dagen";
        if (days === 1) {
          retValue = "dag";
        }
        if (days === 0) {
          retValue = "";
        }
        days = days === 0 ? "vandaag" : days;
        return {
          returnValue: `${days} ${retValue} ${this.masterData.expired}`,
          expired: "expired",
          progressValue: days,
        };
      }

      const retValue = days === 1 ? "dag" : "dagen";
      return {
        returnValue: `verloopt over ${days} ${retValue}`,
        expired: "not-expired",
        progressValue: days,
      };
    },
    deleteContract: function() {
      this.$store.dispatch("deleteContract", { key: this.contract.key });
      this.$store.dispatch("setContracts");
    },
    editContract: function() {
      this.$modal.show(
        EditcontractModal,
        {
          title: "Wijzig contract",
          id: this.contract.id,
          name: this.contract.name,
          category: this.categoryValue,
          customer: this.customerValue,
          file: this.fileValue,
          contactValue: this.contactsValue,
          contractDateFrom: moment(
            new Date(this.contract.dateTime[0].toDate())
          ).format("YYYY-MM-DD"),
          contractDateTill: moment(
            new Date(this.contract.dateTime[1].toDate())
          ).format("YYYY-MM-DD"),
          daysBeforeNotification: this.daysBeforeNotificationValue,
          contract_key: this.contract.key,
        },
        {
          scrollable: true,
          height: "auto",
        }
      );
    },
  },
  props: ["contract", "files", "masterData", "firstCat", "contacts"],
  name: "Contract",
  components: {
    ProgressBar,
  },
  filters: {},
};
</script>

