<template>
  <div class="progressBar">
    <div
      class="progressBar__value"
      :style="{
        width: percentageOfValues + '%',
        backgroundColor: percentageColor
      }"
    ></div>
  </div>
</template>

<script>
export default {
  name: "ProgressBar",
  data: function() {
    return {};
  },
  computed: {
    percentageOfValues() {
      if (this.expired === "expired") {
        return 100;
      }

      if (this.days < 10) {
        return 90;
      }
      if (this.days < 20) {
        return 80;
      }
      if (this.days < 30) {
        return 70;
      }
      if (this.days < 40) {
        return 60;
      }
      if (this.days < 50) {
        return 50;
      }
      if (this.days < 60) {
        return 40;
      }
      if (this.days < 70) {
        return 30;
      }
      if (this.days < 80) {
        return 20;
      }
      if (this.days < 90) {
        return 10;
      }
      return 5;
    },
    percentageColor() {
      if (this.percentageOfValues < 25) {
        return "#5ab35a";
      }
      if (this.percentageOfValues < 50) {
        return "#f7bf1b";
      }
      if (this.percentageOfValues < 75) {
        return "#f79a26";
      }
      if (this.percentageOfValues < 101) {
        return "#f7401c";
      }
      return "#f7401c";
    }
  },
  methods: {},
  props: ["days", "expired"],
  components: {},
  filters: {}
};
</script>

<style lang="scss" scope>
.progressBar {
  width: 200px;
  background-color: #d8d8d8;
  height: 4px;
  margin-bottom: -2px;
  &__value {
    height: 4px;
    position: absolute;
    left: 0;
    top: 0;
  }
}
</style>
