import Vue from "vue";
import Vuex from "vuex";
import { db } from "@/main";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    contracts: [],
    customers: [],
    masterData: {},
    categories: [],
    documents: [],
    files: [],
    contacts: [],
    daysBeforeNotification: []
  },
  getters: {
    getContracts: state => {
      const _categoriesArray = [];
      return state.contracts
        .slice()
        .sort((a, b) => {
          if (a.category < b.category) {
            return -1;
          }
          if (b.category < a.category) {
            return 1;
          }
          return 0;
        })
        .map(contract => {
          if (_categoriesArray.includes(contract.category)) {
            contract.isFirst = false;
          } else {
            _categoriesArray.push(contract.category);
            contract.isFirst = true;
          }
          return contract;
        });
    },
    getCustomers: state => {
      return state.customers;
    },
    getMasterData: state => {
      return state.masterData;
    },
    getCategories: state => {
      return state.categories;
    },
    getDocuments: state => {
      return state.documents;
    },
    getFiles: state => {
      return state.files;
    },
    getContacts: state => {
      return state.contacts;
    },
    getDaysBeforeNotification: state => {
      return state.daysBeforeNotification;
    }
  },
  mutations: {
    setContracts: state => {
      let contracts = [];
      db.collection("contracts").onSnapshot(snapshot => {
        contracts = [];
        snapshot.forEach(doc => {
          contracts.push({
            id: doc.data().id,
            key: doc.id,
            category: doc.data().category,
            customer: doc.data().customer,
            dateTime: doc.data().dateTime,
            daysBeforeNotification: doc.data().daysBeforeNotification,
            documentId: doc.data().documentId,
            contacts: doc.data().contacts,
            name: doc.data().name
          });
        });
        const _categoriesArray = [];
        const contacten = contracts
          .slice()
          .sort((a, b) => {
            if (a.category < b.category) {
              return -1;
            }
            if (b.category < a.category) {
              return 1;
            }
            return 0;
          })
          .map(contract => {
            if (_categoriesArray.includes(contract.category)) {
              contract.isFirst = false;
            } else {
              _categoriesArray.push(contract.category);
              contract.isFirst = true;
            }
            return contract;
          });
        state.contracts = contacten;
      });
    },
    setCustomers: state => {
      let customers = [];
      db.collection("customers").onSnapshot(snapshot => {
        customers = [];
        snapshot.forEach(doc => {
          customers.push({
            key: doc.id,
            id: doc.data().id,
            name: doc.data().name,
            image: doc.data().image
          });
        });
        state.customers = customers;
      });
    },
    setContacts: state => {
      let contacts = [];
      db.collection("contacts").onSnapshot(snapshot => {
        contacts = [];
        snapshot.forEach(doc => {
          contacts.push({
            key: doc.id,
            id: doc.data().id,
            name: doc.data().name,
            mail: doc.data().mail,
            slackName: doc.data().slackName
          });
        });
        state.contacts = contacts;
      });
    },
    setDocuments: state => {
      let documents = [];
      db.collection("documents").onSnapshot(snapshot => {
        documents = [];
        snapshot.forEach(doc => {
          documents.push({
            key: doc.id,
            id: doc.data().id,
            extension: doc.data().extension,
            name: doc.data().name,
            url: doc.data().url
          });
        });
        state.documents = documents;
      });
    },
    setCategories: state => {
      let categories = [];
      db.collection("categories").onSnapshot(snapshot => {
        categories = [];
        snapshot.forEach(doc => {
          categories.push({
            key: doc.id,
            id: doc.data().id,
            name: doc.data().name
          });
        });
        state.categories = categories;
      });
    },
    setDaysBeforeNotification: state => {
      let notifications = [];
      db.collection("daysBeforeNotifications").onSnapshot(snapshot => {
        notifications = [];
        snapshot.forEach(doc => {
          notifications.push({
            key: doc.id,
            id: doc.data().id,
            amount: doc.data().amount,
            name: doc.data().name
          });
        });
        state.daysBeforeNotification = notifications;
      });
    },
    setMasterData: state => {
      let masterData = {};
      db.collection("masterdata").onSnapshot(snapshot => {
        masterData = {};
        masterData.siteUrl = snapshot.docs[0].data().siteUrl;
        masterData.expired = snapshot.docs[0].data().expired;
        state.masterData = masterData;
      });
    },
    setFiles: state => {
      let files = [];
      db.collection("files").onSnapshot(snapshot => {
        files = [];
        snapshot.forEach(doc => {
          files.push({
            key: doc.id,
            fileId: doc.data().id,
            path: doc.data().path,
            extension: doc.data().extension
          });
        });
        state.files = files;
      });
    },
    addContract: (state, data) => {
      const filteredCats = state.contracts.sort((a, b) => {
        return a.id - b.id;
      });
      const lastCat =
        filteredCats.length > 0
          ? filteredCats[filteredCats.length - 1].id + 1
          : 0;
      const contacts = data.contacts.map(({ id }) => {
        return id;
      });
      const notificationDays = data.daysBeforeNotification.map(({ id }) => {
        return id;
      });

      const contractDateFromInSeconds = new Date(data.contractDateFrom);
      const contractDateTillInSeconds = new Date(data.contractDateTill);

      db.collection("contracts")
        .add({
          name: data.name,
          category: data.category,
          customer: data.customer,
          documentId: data.file,
          contacts: contacts,
          dateTime: [contractDateFromInSeconds, contractDateTillInSeconds],
          daysBeforeNotification: notificationDays,
          id: lastCat
        })
        .then(() => {
          console.log("succesvol toegevoegd");
        });
    },
    addCategory: (state, data) => {
      const filteredCats = state.categories.sort((a, b) => {
        return a.id - b.id;
      });
      const lastCat = filteredCats[filteredCats.length - 1].id + 1;
      db.collection("categories")
        .add({
          name: data.name,
          id: lastCat
        })
        .then(() => {
          console.log("succesvol toegevoegd");
        });
    },
    addCustomer: (state, data) => {
      const filteredCats = state.customers.sort((a, b) => {
        return a.id - b.id;
      });
      const lastCat = filteredCats[filteredCats.length - 1].id + 1;
      db.collection("customers")
        .add({
          name: data.name,
          id: lastCat,
          image: data.image
        })
        .then(() => {
          console.log("succesvol toegevoegd");
        });
    },
    editCategory: (state, data) => {
      const category = db.collection("categories").doc(data.key);
      category.update({ name: data.name }).then(() => {
        console.log("succesvol aangepast");
      });
    },
    editContact: (state, data) => {
      const contact = db.collection("contacts").doc(data.key);
      contact
        .update({ name: data.name, mail: data.mail, slackName: data.slackName })
        .then(() => {
          console.log("succesvol aangepast");
        });
    },
    editCustomer: (state, data) => {
      const customer = db.collection("customers").doc(data.key);
      customer.update({ name: data.name, image: data.image }).then(() => {
        console.log("succesvol aangepast");
      });
    },
    editContract: (state, data) => {
      const contract = db.collection("contracts").doc(data.contract_key);

      const contractDateFromInSeconds = new Date(data.contractDateFrom);
      const contractDateTillInSeconds = new Date(data.contractDateTill);

      contract
        .update({
          name: data.name,
          category: data.category.id,
          customer: data.customer.id,
          file: data.files[0].id,
          contacts: data.contacts,
          dateTime: [contractDateFromInSeconds, contractDateTillInSeconds],
          daysBeforeNotification: data.daysBeforeNotification
        })
        .then(() => {
          console.log("succesvol aangepast");
        });
    },
    deleteCategory: (state, data) => {
      const category = db.collection("categories").doc(data.key);
      category.delete().then(() => {
        console.log("succesvol verwijdert");
      });
    },
    deleteCustomer: (state, data) => {
      const customer = db.collection("customers").doc(data.key);
      customer.delete().then(() => {
        console.log("succesvol verwijdert");
      });
    },
    deleteContract: (state, data) => {
      const contract = db.collection("contracts").doc(data.key);
      contract.delete().then(() => {
        console.log("succesvol verwijdert");
      });
      state.contracts.forEach((contract, index) => {
        if (contract.key === data.key) {
          const deletedItem = state.contracts.splice(
            state.contracts.indexOf(index),
            1
          );
          state.contracts = deletedItem;
        }
      });
    }
  },
  actions: {
    setContracts: context => {
      context.commit("setContracts");
    },
    setCustomers: context => {
      context.commit("setCustomers");
    },
    setMasterData: context => {
      context.commit("setMasterData");
    },
    setCategories: context => {
      context.commit("setCategories");
    },
    setDaysBeforeNotification: context => {
      context.commit("setDaysBeforeNotification");
    },
    setDocuments: context => {
      context.commit("setDocuments");
    },
    setFiles: context => {
      context.commit("setFiles");
    },
    setContacts: context => {
      context.commit("setContacts");
    },
    addCategory({ commit }, data) {
      commit("addCategory", data);
    },
    addCustomer({ commit }, data) {
      commit("addCustomer", data);
    },
    addContract({ commit }, data) {
      commit("addContract", data);
      // commit("setContracts");
    },
    editCategory({ commit }, data) {
      commit("editCategory", data);
    },
    editCustomer({ commit }, data) {
      commit("editCustomer", data);
    },
    editContact({ commit }, data) {
      commit("editContact", data);
    },
    editContract({ commit }, data) {
      commit("editContract", data);
    },
    deleteCategory({ commit }, data) {
      commit("deleteCategory", data);
    },
    deleteCustomer({ commit }, data) {
      commit("deleteCustomer", data);
    },
    deleteContract({ commit }, data) {
      commit("deleteContract", data);
    }
  },
  modules: {}
});
